.nav {
    height: 100px;
    background-image: url(https://c4.wallpaperflare.com/wallpaper/453/584/943/3d-triangle-abstract-4k-wallpaper-preview.jpg);

}

.nav-bar {
    display: flex;
    justify-content: right;
    gap: 40px;
    color: blanchedalmond;

}

.nav-bar>li {
    display: flex;
    list-style-type: none;
    margin-top: 40px;
}

.li,
a {
    font-size: 20px;
    color: blanchedalmond;
    text-decoration: none;
    font-family: 'B612 Mono', monospace;
    font-family: 'Chivo Mono', monospace;
    font-family: 'Lora', serif;
}

.li,
a :hover{
    background-color: blanchedalmond;
    color:rgb(215, 116, 51)
}